import ModalContainer from '../../components/ModalContainer';
import { useForm } from 'react-hook-form';
import * as Yup from "yup"
import { yupResolver } from '@hookform/resolvers/yup';
import { post } from '../../application/middlewares/index';
import {useDispatch} from 'react-redux'

import toast from 'react-hot-toast';
import { useEffect, useState } from 'react';
import BgLoader from '../../components/BgLoader';
import ImageUpload from '../../components/ImageUpload';

function Form ({visible, setCloseModal, form, title, token, values, addCategory, updateCategory, setUploadedImage}){
    // console.log(values, "  ----values")
    const {register,  handleSubmit, formState: { errors },  reset, setValue} = useForm({
        resolver:yupResolver(shcema),
        defaultValues:{enabled:false}
    })
    const dispatch = useDispatch()

    useEffect(() =>{
        if(visible === true && (form === 'update' || form === 'update_main_form')){
            Object.keys(values).forEach(key =>{
                if(key !== 'id'){
                    setValue(key, values[key])

                }
            })
        }else{
            reset({enabled:false})
        }
    }, [visible]); // eslint-disable-line react-hooks/exhaustive-deps
    const [loading, setLoading] = useState(false)
    const onSubmit = (data)=>{   
        // console.log("i am in submit")
        setLoading(true)
        dispatch(post({
            url: form === "update" ? `api/admin/update-category/${values.id}` : `api/admin/add-category`,
            data,
            token,
            action : (response) =>{
                if(response.success){
                    toast.success("Good yu have added")
                    if(form === "update"){
                        updateCategory({id:values.id, ...data})
                        setCloseModal()
                        setLoading(true)                        
                    }else{
                        addCategory(response.data.rows)
                        setCloseModal()
                    }
                }else{
                    toast.error("Something went wrong")
                    // console.log("Some bad error")
                }
            }
        }))
    }
    // useEffect(()=>{
    //     console.log(form)
    // }, [form])
    return (
        <ModalContainer size="md" 
            setCloseModal={() => {
                setCloseModal();
                // reset({enabled:false})
            }} 
            visible={visible} 
            title={title}
        >
            {form === "image" ?
                <ImageUpload
                    visible={true}
                    myDocument={values}
                    token={token}
                    setUploadedImage={setUploadedImage}
                    url={`/api/admin/add-category-image/${values?.id}`}
                />
            :
            
            <form onSubmit = {handleSubmit(onSubmit)}>
            <div className = "flex flex-col">
                <div className="relative w-full mb-6">
                    <label>На туркменском</label>
                    <input type="text" {...register("name_tm")}
                        autoComplete="false"
                        className={`${errors.name_tm ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="Saç üçin"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.name_tm?.message}
                    </p>
                </div>
                <div className="relative w-full mb-6">
                    <label>На русском</label>
                    <input type="text" {...register("name_ru")}
                        autoComplete="false"
                        className={`${errors.name_ru ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="Для вас"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.name_ru?.message}
                    </p>
                </div>
                <div className="relative w-full mb-6">
                    <label>На английском</label>
                    <input type="text" {...register("name_en")}
                        autoComplete="false"
                        className={`${errors.name_en ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                        placeholder="For hair"
                    />
                    <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                        {errors.name_en?.message}
                    </p>
                </div>
            </div>
            <div className="w-full flex justify-center items-center absolute -bottom-6">
                <button type="submit"  className="w-50 flex remove-button-bg justify-center items-center px-4 h-10 text-white transform ease-in-out duration-300 hover:scale-110 active:scale-100 font-semibold text-base rounded-full bg-green-500 hover:bg-green-400 active:bg-green-500 focus:outline-none shadow-md">
                    {loading 
                            ?
                            <BgLoader loading = {loading}/>
                            :
                            <div>{form === "update" ? "Изменить" : "Добавить"}</div>
                        }
                </button>
            </div>
            </form>
        }
        </ModalContainer>
    )
}

const shcema = Yup.object().shape({
    name_tm:Yup.string().required('Перевод обязателен').min(3, 'Минимум 3 символов').max(50, 'Максимум 50 символов'),
    name_ru:Yup.string().required('Перевод обязателен').min(3, 'Минимум 3 символов').max(50, 'Максимум 50 символов'),
    name_en:Yup.string().required('Перевод обязателен').min(3, 'Минимум 3 символов').max(50, 'Максимум 50 символов'),
})

export default Form